.ProyectContainer {
  position: relative;
  background-color: #191716;
  width: 100%;
}

.firstProyectPhone {
  width: 100%;
  height: 100%;
  background-color: #50c878;
}

.ProyectSpacer {
  padding-top: 10px;
}

.bodyText6 {
  font-family: "Lexend", sans-serif;
  font-size: 5rem;
  color: #50c878;
}

.ProyectChip {
  background-color: #333333;
  margin: 20px 70px;
  border-radius: 20px 20px 20px 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  min-width: 600px;
}

.ProyectChipSmall {
  background-color: #333333;
  border-radius: 20px 20px 20px 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  min-width: 300px;
  height: 400px;
  margin: 20px 70px;
}

.PhoneDelimiter {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-left: 10px;
}

.chipdiv {
  margin-left: 15px;
  width: 100%;
}

.chipTitle {
  color: white;
  font-family: "Lexend Light", sans-serif;
  white-space: nowrap;
  font-size: 2rem;
  padding-left: 4px;
  font-weight: bold;
}

.imageChip {
  max-width: 300px;
  border-radius: 20px 20px 20px 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

@media (max-width: 700px) {
  .imageChip {
    min-width: 100%;
    max-height: 250px;
    border-radius: 20px 20px 20px 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }

  .chipPlainText {
    color: white;
    font-family: "Lexend Light", sans-serif;
    font-size: 1rem;
    margin-right: 10px;
    margin-left: 10px;
    text-align: center;
  }
}

.chipPlainText {
  color: white;
  font-family: "Lexend Light", sans-serif;
  font-size: 1rem;
  padding-left: 7px;
}

.gotoButton {
  color: #50c878 !important;
  font-weight: bold !important;
  width: 50px;
}

.gotoButton:hover {
  background-color: rgba(80, 200, 120, 0.36) !important;
}

@keyframes fadeInFromLeft {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fadeInFromRight {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.chipAnimLeft {
  animation: fadeInFromLeft 1s ease forwards;
}

.chipAnimRight {
  animation: fadeInFromRight 1s ease forwards;
}
